import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { CardGoodsComponent } from './card-goods.component';
import { RouterModule, Routes } from '@angular/router';
import { NgZorroAntModule } from 'src/app/cafe/desktop/ng-zorro-ant.module';
import { IonicModule } from '@ionic/angular';
import { TranslateModule } from '@ngx-translate/core';


const routes: Routes = [
  {
    path: '',
    component: CardGoodsComponent
  },
  {
    path: 'list-card',
    loadChildren: () => import('./list-card/list-card.module').then(m => m.ListCardModule)
  },
  {
    path: 'releases',
    loadChildren: () => import('./releases/releases.module').then(m => m.ReleasesModule)
  },
  {
    path: 'sell-card',
    loadChildren: () => import('./sell-card/sell-card.module').then(m => m.SellCardModule)
  },
  {
    path: 'report',
    loadChildren: () => import('./report/report.module').then(m => m.ReportModule)
  },
  {
    path: 'design',
    loadChildren: () => import('./design/design.module').then(m => m.DesignModule)
  },
  {
    path: 'exchange-card-invoice',
    loadChildren: () => import('./exchange-card-invoice/exchange-card-invoice.module').then(m => m.ExchangeCardInvoiceModule)
  },
  {
    path: 'exchange-points',
    loadChildren: () => import('./exchange-points/exchange-points.module').then(m => m.ExchangePointsModule)
  },
]

@NgModule({
  declarations: [
    CardGoodsComponent
  ],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    IonicModule,
    TranslateModule
  ]
})
export class CardGoodsModule { }
